    /**
     * Language extensions used by this converter
     * @private
     * @type {Array}
     */
    langExtensions = [],


    /**
     * Output modifiers extensions used by this converter
     * @private
     * @type {Array}
     */
    outputModifiers = [],


    /**
     * Event listeners
     * @private
     * @type {{}}
     */
    listeners = {},


    /**
     * The flavor set in this converter
     */
